import React from "react";
import {BrowserRouter as Router, Switch, Route, Link} from "react-router-dom";

import './App.css';
import Projects from './Projects';
import Footer from './Footer';

export default function App() {
  return (
    <Router>
      <div class="nk-content-body">
        <div class="nk-content-wrap">
          <ul>
            <li>
              <Link to="/">Homex</Link>
            </li>
            <li>
              <Link to="/about">About</Link>
            </li>
            <li>
              <Link to="/dashboard">Dashboard</Link>
            </li>
            <li>
              <Link to="/projects/amm">Dashboard</Link>
            </li>
          </ul>

          <hr />

          <Switch>
            <Route exact path="/">
              <Home />
            </Route>
            <Route path="/about">
              <About />
            </Route>
            <Route path="/dashboard">
              <Dashboard />
            </Route>
            <Route path="/projects/amm">
              <Projects category="Automated Market Makers (AMM)" url="amm.json"/>
            </Route>
          </Switch>
        </div>
        <Footer />
      </div>
    </Router>
  );
}

function Home() {
  return (
    <div>
      <h2>Home</h2>
    </div>
  );
}

function About() {
  return (
    <div>
      <h2>About</h2>
    </div>
  );
}

function Dashboard() {
  return (
    <div>
      <h2>Dashboard</h2>
    </div>
  );
}
