import React from "react";
import Advertisement from './Advertisement';

export default function Projects(props) {
    const {category, url} = props;
    const [projectData, setProjectData] = React.useState([]);
    console.log(url);

    const getJsonData = () => {
        fetch(url, {
            headers : {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            }
        })
        .then(function(response){
            console.log(response);
            return response.json();
        })
        .then(function(myJson) {
            console.log(myJson);
            setProjectData(myJson);
        });
    }

    React.useEffect(() => {
        getJsonData()
    }, []);

  return (
    <>
    {/* Page Header*/}
    <div class="nk-block-head nk-block-head-lg">
        <div class="nk-block-head-sub"><span></span></div>
        <div class="nk-block-between-md g-4">
            <div class="nk-block-head-content">
                <h2 class="nk-block-title fw-normal">{category}</h2>
                <div class="nk-block-des">
                    <p>You can download all the application that available in your plan.</p>
                </div>
            </div>
        </div>
    </div>

    <Advertisement />

    {/* Project Cards*/}
    <div class="nk-block">
      <div class="row g-gs">
        {/*Single Project*/}
        {projectData && projectData.length > 0 && projectData.map((item) =>
        <div class="col-sm-6 col-xl-4">
            <div class="card card-bordered h-100">
                <div class="card-inner">
                    <div class="project">
                        <div class="project-head">
                            <a href="html/apps-kanban.html" class="project-title">
                                <div class="user-avatar sq bg-purple"><span>
                                    <img src={item.icon} />
                                </span></div>
                                <div class="project-info">
                                    <h6 class="title">{item.name}</h6>
                                    <span class="sub-text">{item.url}</span>
                                </div>
                            </a>
                            <div class="drodown">
                                <a href="#" class="dropdown-toggle btn btn-sm btn-icon btn-trigger mt-n1 mr-n1" data-toggle="dropdown"><em class="icon ni ni-more-h"></em></a>
                                <div class="dropdown-menu dropdown-menu-right">
                                    <ul class="link-list-opt no-bdr">
                                        <li><a href="html/apps-kanban.html"><em class="icon ni ni-eye"></em><span>View Project</span></a></li>
                                        <li><a href="#"><em class="icon ni ni-edit"></em><span>Edit Project</span></a></li>
                                        <li><a href="#"><em class="icon ni ni-check-round-cut"></em><span>Mark As Done</span></a></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div class="project-details">
                            <p>{item.description}</p>
                        </div>
                        <div class="project-meta">
                            <ul class="project-users g-1">
                                {item.social.twitter.length > 0 &&
                                <li>
                                    <span class="nk-menu-icon">
                                        <a href={item.social.twitter} target="_blank">
                                            <em class="icon ni ni-twitter"></em>
                                        </a>
                                    </span>
                                </li>
                                }
                                {item.social.telegram.length > 0 &&
                                <li>
                                    <span class="nk-menu-icon">
                                        <a href={item.social.telegram} target="_blank">
                                            <em class="icon ni ni-telegram"></em>
                                        </a>
                                    </span>
                                </li>
                                }
                                {item.social.discord.length > 0 &&
                                <li>
                                    <span class="nk-menu-icon">
                                        <a href={item.social.discord} target="_blank">
                                            <em class="icon ni ni-chat-circle-fill"></em>
                                        </a>
                                    </span>
                                </li>
                                }
                                {item.social.medium.length > 0 &&
                                <li>
                                    <span class="nk-menu-icon">
                                        <a href={item.social.medium} target="_blank">
                                            <em class="icon ni ni-medium"></em>
                                        </a>
                                    </span>
                                </li>
                                }
                                {item.social.reddit.length > 0 &&
                                <li>
                                    <span class="nk-menu-icon">
                                        <a href={item.social.reddit} target="_blank">
                                            <em class="icon ni ni-reddit"></em>
                                        </a>
                                    </span>
                                </li>
                                }
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        )}
      </div>
    </div>
    </>
  );
}